@import url(https://fonts.googleapis.com/css?family=Lato:400,700|Montserrat:400,700|Poppins:400,700|Roboto:400,700&display=swap&subset=latin-ext);
html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #efefef;
}
a {
  -webkit-transition: all 0.5 ease-in-out;
  transition: all 0.5 ease-in-out;
}

a:hover {
  text-decoration: none;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

#card_ant {
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
align-items: center;  font-family: 'apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji' !important;
}

#card_pdf_preview {
  -webkit-transition: all 0.1s ease-in-out !important;
  transition: all 0.1s ease-in-out !important;

  justify-self: self-end !important;
  margin-right: 5px !important;
  -webkit-transform: scale(1.3) !important;
          transform: scale(1.3) !important;
  margin-top: 110px !important;
  overflow: hidden;

  font-family: 'apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji' !important;
}

#card_pdf:hover, #card_ant_film:hover,.ant-card.ant-card-bordered {

  box-shadow: 1px 1px 20px 5px rgba(0,0,0,0.75)
}

#card_film_preview  {
 -webkit-transition: all 0.1s ease-in-out !important;
 transition: all 0.1s ease-in-out !important;

  justify-self: self-end !important;
  margin-right: 5px !important;
  -webkit-transform: scale(1.1) !important;
          transform: scale(1.1) !important;
  margin-top: 30px !important;
  overflow: hidden;

  font-family: 'apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji' !important;
}



.ant-form.ant-form-horizontal {
  justify-self: self-start;
  padding: 50px;
  padding-left: 0;
  padding-right: 130px;
  margin-left: 0;
  width: 100%;
}
.ant-col.ant-form-item-label.ant-col-xs-24.ant-col-sm-8 {
  margin: 0;
}
.ant-row.ant-form-item {
  margin: 0 0 9px 0 !important;
}

.burger {
  background: none;
  border: none;
  padding: 0;
  font-size: 24px;
  -webkit-transform: scaleX(1.5);
          transform: scaleX(1.5);
  display: none;
}

ul li {
  opacity: 0.7;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
}
ul li:hover {
  opacity: 1;
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.animated.fadeIn {
  max-width: 100% !important;
}

#type_cards_wrapper {
  margin-bottom: 60px  !important;
}

input[type='file']{
  height: 1px;
}

label[name='Wybierz plik']{
  margin-top: 40px;
}



#card_ant_film .ant-card-bod, #card_film_preview .ant-card-body{
height: 60px;
padding: 20px;
padding-right: 35px;
}








@media (max-width: 768px) {
  .burger {
    display: inline-block;
  }
  ul {
    /* display: none !important; */
    flex-direction: column;
    width: 100%;
  }
  nav {
    flex-wrap: wrap !important;
    text-align: center;
  }
  ul li {
    /* display:none; */
    margin: 15px;
  }

  div[style='display: grid; grid-template-columns: 30% 70%; width: 100%;'], div[style='display: grid; grid-template-columns: 40% 60%; width: 100%;'] {
    display: flex !important;
    flex-wrap: wrap !important;
    justify-content: center !important;
    align-items: center !important;
  }

  #card_pdf_preview {
    -webkit-transform: scale(1) !important;
            transform: scale(1) !important;
    margin-top: 0px !important;
  }

  .ant-form.ant-form-horizontal {
    justify-self: self-start;
    padding: 10px;
    margin-left: 0;
    width: 90%;
  }
  .ant-col.ant-form-item-label.ant-col-xs-24.ant-col-sm-8,
  .ant-form-item-required {
    padding: 0 !important;
    margin: 0 !important;
  }
  .ant-row.ant-form-item {
    margin: 0 0 7px 0 !important;
  }


  #card_pdf {
    display: block !important;
    margin: 0 auto !important;

  }
  h5{
    text-align: center !important;
  }
}

@media (min-width: 769px) {
  ul {
    display: flex !important;
  }
}

